.btnBack {
    display: block;
    width: 60px;
    cursor: pointer;
    border: none;
    padding: 10px 20px;
    background-color: rgb(219, 208, 230);
    color: violet;
    border-radius: 10px;
    font-weight: 500;
    text-decoration: none;
    text-align: center;
  }
  
  .btnBack:hover {
    background-color: blueviolet;
    color: white;
  }
  
  .btnBack:active {
    box-shadow: 12px 3px 54px -23px rgba(0, 0, 0, 0.75) inset;
    -webkit-box-shadow: 12px 3px 54px -23px rgba(0, 0, 0, 0.75) inset;
    -moz-box-shadow: 12px 3px 54px -23px rgba(0, 0, 0, 0.75) inset;
  }